import React from "react"
import { Link as GatsbyLink, graphql } from "gatsby"
import { getSrc } from "gatsby-plugin-image"
import parse from "html-react-parser"
import { Link, Box, Text } from "@chakra-ui/react"
import {
  PostArticle,
  PostTitle,
  PostMeta,
  FeatImage,
  ArticleBody,
} from "../../styles/postStyles"

import { postOptions } from "../../parseOptions"

// import ShareButtons from "../../components/ShareButtons"


// We're using Gutenberg so we need the block styles
// import "@wordpress/block-library/build-style/style.css"
// import "@wordpress/block-library/build-style/theme.css"

import Layout from "../../components/layout"
import Seo from "../../components/Seo"

const BlogPostTemplate = ({ data: { previous, next, post }, location }) => {
  const featuredImage = {
    fluid:
      post.featuredImage?.node?.localFile?.childImageSharp?.gatsbyImageData,
    fixed:
      post.featuredImage?.node?.localFile?.childImageSharp?.gatsbyImageData
        ?.src,
    aspectRatio: 16 / 9,
    alt: post.featuredImage?.node?.alt || ``,
  }
  const imgSrc = getSrc(post.featuredImage)
  const metaDate = new Date(post.metaDate).toISOString()
  const modifiedDate = new Date(post.modified).toISOString()
  const breadcrumbMeta = post.seo.breadcrumbs.map((crumb, index) => {
    const position = index + 1
    const item = {
      "@type": "ListItem",
      position: position,
      item: {
        "@type": "WebPage",
        "@id": `https://www.idrivesocal.com${crumb.url}`,
        url: `https://www.idrivesocal.com${crumb.url}`,
        name: crumb.text,
      },
    }
    return item
  })

  const cats = post.categories.nodes.map(cat => {
    return cat.name
  })
  const catMeta = cats.join()
  const schema = {
    "@context": "https://schema.org",
    "@graph": [
      {
        "@type": "Organization",
        "@id": "https://www.idrivesocal.com/#organization",
        name: "iDriveSoCal",
        url: "https://www.idrivesocal.com/",
        sameAs: [
          "https://www.facebook.com/idrivesocal/",
          "https://www.instagram.com/idrivesocal/",
          "https://www.linkedin.com/company/idrivesocal/",
          "https://www.youtube.com/channel/UC0HZusGNEBUl3lO7AdprFdQ",
          "https://www.pinterest.com/iDriveSoCal/",
          "https://twitter.com/idrivesocal",
        ],
        logo: {
          "@type": "ImageObject",
          "@id": "https://www.idrivesocal.com/#logo",
          inLanguage: "en-US",
          url:
            "https://www.idrivesocal.com/wp-content/uploads/2019/05/iDriveSoCal.jpg",
          contentUrl:
            "https://www.idrivesocal.com/wp-content/uploads/2019/05/iDriveSoCal.jpg",
          width: 824,
          height: 606,
          caption: "iDriveSoCal",
        },
        image: { "@id": "https://www.idrivesocal.com/#logo" },
      },
      {
        "@type": "WebSite",
        "@id": "https://www.idrivesocal.com/#website",
        url: "https://www.idrivesocal.com/",
        name: "iDriveSoCal",
        description: "Automotive News &amp; Events",
        publisher: { "@id": "https://www.idrivesocal.com/#organization" },
        potentialAction: [
          {
            "@type": "SearchAction",
            target: "https://www.idrivesocal.com/?s={search_term_string}",
            "query-input": "required name=search_term_string",
          },
        ],
        inLanguage: "en-US",
      },
      {
        "@type": "ImageObject",
        "@id": `https://www.idrivesocal.com${post.uri}#primaryimage`,
        inLanguage: "en-US",
        url: imgSrc,
        contentUrl: imgSrc,
        width: 940,
        height: 627,
        caption: featuredImage?.alt,
      },
      {
        "@type": "WebPage",
        "@id": `https://www.idrivesocal.com${post.uri}#webpage`,
        url: `https://www.idrivesocal.com${post.uri}`,
        name: post.title,
        isPartOf: { "@id": "https://www.idrivesocal.com/#website" },
        primaryImageOfPage: {
          "@id": `https://www.idrivesocal.com${post.uri}#primaryimage`,
        },
        datePublished: metaDate,
        dateModified: modifiedDate,
        description: post.seo.metaDesc,
        breadcrumb: {
          "@id": `https://www.idrivesocal.com${post.uri}#breadcrumb`,
        },
        inLanguage: "en-US",
        potentialAction: [
          {
            "@type": "ReadAction",
            target: [`https://www.idrivesocal.com${post.uri}`],
          },
        ],
      },
      {
        "@type": "BreadcrumbList",
        "@id": `https://www.idrivesocal.com${post.uri}#breadcrumb`,
        itemListElement: breadcrumbMeta,
      },
      {
        "@type": ["Article", "NewsArticle"],
        "@id": `https://www.idrivesocal.com${post.uri}#article`,
        isPartOf: {
          "@id": `https://www.idrivesocal.com${post.uri}#webpage`,
        },
        author: {
          "@id":
            "https://www.idrivesocal.com/#/schema/person/3103a769d4bfc520790c66d383460d0c",
        },
        headline: post.title,
        datePublished: metaDate,
        dateModified: modifiedDate,
        mainEntityOfPage: {
          "@id": `https://www.idrivesocal.com${post.uri}#webpage`,
        },
        commentCount: 0,
        publisher: { "@id": "https://www.idrivesocal.com/#organization" },
        image: {
          "@id": `https://www.idrivesocal.com${post.uri}#primaryimage`,
        },
        keywords: post.seo.focuskw,
        articleSection: catMeta,
        inLanguage: "en-US",
        potentialAction: [
          {
            "@type": "CommentAction",
            name: "Comment",
            target: [`https://www.idrivesocal.com${post.uri}#respond`],
          },
        ],
        copyrightYear: "2021",
        copyrightHolder: { "@id": "https://www.idrivesocal.com/#organization" },
      },
      {
        "@type": "Person",
        "@id":
          "https://www.idrivesocal.com/#/schema/person/3103a769d4bfc520790c66d383460d0c",
        name: "Tom Smith",
        image: {
          "@type": "ImageObject",
          "@id": "https://www.idrivesocal.com/#personlogo",
          inLanguage: "en-US",
          url:
            "https://secure.gravatar.com/avatar/5aa5f7ce3212b1123ed6e8ae1f486164?s=96&d=mm&r=g",
          contentUrl:
            "https://secure.gravatar.com/avatar/5aa5f7ce3212b1123ed6e8ae1f486164?s=96&d=mm&r=g",
          caption: "Tom Smith",
        },
        description:
          "I grew up in a small town in northern Illinois, graduated from Columbia College Chicago, and produced TV news for network affiliates. The TV news business was fun. But not the hours or many of the stories. And, I shifted to sales. But after many years I started getting fired a lot. Turns out old-school media companies don't like painful honesty and new ideas. Luckily, Al Gore invented the internet. So, I started iDSC. And cover whatever stories I want. ;0)",
        sameAs: ["https://www.linkedin.com/in/tomsmith89/"],
      },
    ],
  }

  return (
    <Layout
      breadcrumbs={post.seo.breadcrumbs}
      cta={post.post_cta}
      tagPosts={post.tags.nodes}
      categories={post.categories.nodes}
      postId={post.id}
      author
      previous={previous}
      next={next}
    >
      <Seo
        title={post.title}
        description={parse(post.excerpt)}
        schemaMarkup={schema}
      />
      <PostArticle itemScope itemType="http://schema.org/Article">
        <header>
          <PostTitle as="h1" px={{ base: 4, sm: 8, md: 8 }} itemProp="headline">
            {parse(post.title)}
          </PostTitle>
          <PostMeta px={{ base: 4, sm: 8, md: 8 }}>
            <Link
              as={GatsbyLink}
              to={post.author.node.uri}
              itemProp="url"
              color="gray.500"
            >
              <span>Posted by {post.author.node.name}</span>
            </Link>
            <span>|</span>
            <span>{post.date}</span>
            {post.categories.nodes && <span>|</span>}
            {post.categories.nodes &&
              post.categories.nodes.map((cat, index, cats) => {
                return (
                  <Link
                    as={GatsbyLink}
                    to={cat.uri}
                    itemProp="url"
                    color="gray.500"
                    key={index}
                  >
                    <span key={index}>
                      {cat.name}
                      {index < cats.length - 1 && ","}
                    </span>
                  </Link>
                )
              })}
          </PostMeta>

          {/* if we have a featured image for this post let's display it */}
          {featuredImage?.fluid && (
            <FeatImage image={featuredImage.fluid} alt={featuredImage.alt} />
          )}
        </header>

        {!!post.content && (
          <ArticleBody p={8} itemProp="articleBody">
            {parse(post.content, postOptions)}
          </ArticleBody>
        )}

        <hr />
      </PostArticle>
      <Box
        mx="auto"
        py="4"
        display="flex"
        flexDirection="row"
        justifyContent="center"
        alignItems="center"
      >
        <Text pr="4" textTransform="uppercase">
          Share: 
        </Text>
      </Box>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostById($id: String!, $previousPage: String, $nextPage: String) {
    post: wpPost(id: { eq: $id }) {
      id
      uri
      excerpt
      content
      title
      date(formatString: "MMM DD, YYYY")
      metaDate: date
      modified
      seo {
        metaDesc
        focuskw
        breadcrumbs {
          text
          url
        }
      }
      author {
        node {
          id
          uri
          name
        }
      }
      categories {
        nodes {
          name
          uri
        }
      }
      tags {
        nodes {
          posts {
            nodes {
              id
              uri
              title
              featuredImage {
                node {
                  altText
                  localFile {
                    childImageSharp {
                      gatsbyImageData(
                        width: 400
                        placeholder: TRACED_SVG
                        layout: CONSTRAINED
                      )
                    }
                  }
                }
              }
            }
          }
        }
      }
      post_cta {
        callToAction
        callToActionLink
      }
      featuredImage {
        node {
          altText
          localFile {
            childImageSharp {
              gatsbyImageData(placeholder: TRACED_SVG, layout: FULL_WIDTH)
            }
          }
        }
      }
    }
    previous: wpPost(id: { eq: $previousPage }) {
      uri
      title
    }
    next: wpPost(id: { eq: $nextPage }) {
      uri
      title
    }
  }
`
