import React from "react"
import { Stack, HStack, Box, Button, Heading, Text, chakra } from "@chakra-ui/react"
import { Formik, Form, Field, ErrorMessage } from "formik"
import TextField from "./TextField"
import * as Yup from "yup"
import addToMailchimp from "gatsby-plugin-mailchimp"
import lowerBg from "./lowerBg.svg"
import { StaticQuery, graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"

const FormImage = chakra(GatsbyImage, {
  baseStyle: {
    maxWidth: "40%",
    overflow: "hidden",
  },
})

const InlineForm = () => {
  const validate = Yup.object({
    email: Yup.string()
      .email("Please provide a valid email address")
      .required("A valid email address is required"),
  })
  const verifyCallback = response => {
    console.log(response)
  }
  return (
    <StaticQuery 
    query={graphql`
    query {
      image: wpMediaItem(databaseId: {eq: 9717}) {
        altText
        localFile {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  `}
  render={data => (
    <Stack borderColor="brand.500" borderWidth="5px" my={8}>
      <Stack
        py={6}
        px={6}
        direction="row"
      >
        <FormImage image={data.image.localFile.childImageSharp.gatsbyImageData} alt={data.image.altText} />
        <Box textAlign="center" px={8}>
          <Heading fontSize="1.5rem" fontWeight="normal" color="rgba(0,0,0,0.8)">Keep It New Program</Heading>
          <Text fontSize="14px" fontWeight="normal" color="rgba(0,0,0,0.5)">FREE Maintenance For 3-Years!</Text>
          <Text fontSize="14px" fontWeight="normal" color="rgba(0,0,0,0.5)">It's a $1,300 value only at Martin Chevrolet.</Text>
          <Text fontSize="14px" fontWeight="normal" color="rgba(0,0,0,0.5)">
            Subscribe to the <em>iDriveSoCal</em> newsletter and we'll email the offer.
          </Text>
        </Box>
      </Stack>
      <Stack>
        <Formik
          initialValues={{
            email: "",
          }}
          validationSchema={validate}
          onSubmit={async values => {
            const result = await addToMailchimp(values.email, values)
            console.log(result)
          }}
        >
          {formik => (
            <HStack as={Form} bgColor="brand.400" pt={9} pb={8} px={8} spacing="0.8rem">
              <TextField
                size="md"
                type="email"
                label="Email"
                name="email"
                placeholder="Email"
              />
              <Button
                size="md"
                colorScheme="brand"
                textTransform="uppercase"
                type="submit"
                px={8}
              >
                Subscribe!
              </Button>
            </HStack>
          )}
        </Formik>
      </Stack>
    </Stack>
  )}
    />
    
  )
}

export default InlineForm
